import React, { useState, useEffect } from 'react';

import Navbar from '../../components/Navbar/Navbar';

import CardData from '../../data/CardData';

import Box from '../../components/Box/Box';
import Card from '../../components/Card/Card';

import FilterButton from '../../components/FilterButton/FilterButton';

function Works() {
  const [filter, setFilter] = useState('All');

  const FILTER_MAP = {
    All: () => true,
    Models: card => card.category === "models",
    WebApps: card => card.category === "web apps",
    // Other: card => card.category === "other"
  };

  const FILTER_NAMES = Object.keys(FILTER_MAP);

  const filterList = FILTER_NAMES.map(name => (
    <FilterButton
      key={name}
      name={name}
      isPressed={name === filter}
      setFilter={setFilter}
    />

  ));

  const projList = CardData
    .filter(FILTER_MAP[filter])
    .map((card, index) =>
      <Card
        key={index}
        title={card["title"]}
        descr={card['descr']}
        to={card["to"]}
        techused={card["techused"]}
        thumbnail={card["thumbnail"]}
        tag={card["tag"]}
      />
    );

  return (
    <>
      <div>
      
        <section id="portfolio" className="s-portfolio s-portfolio__bg">
        <Navbar></Navbar>
          <div className="row s-portfolio__header">
            <div className="column large-12">
              <h3>Latest Projects</h3>

              {filterList}

            </div>

          </div>

          <div className="row collapse block-large-1- block-medium-1-2 block-tab-1-2 block-500-stack folio-list">

            <Box>

              {projList}

            </Box>

          </div> {/* end folio-list */}

        </section> {/* end s-portfolio */}
      </div>
    </>
  )
}

export default Works;