import React, { useState } from 'react';
import Popup from 'reactjs-popup';

import './card.css';

function Card({ to, title, thumbnail, descr, techused, tag}) {

    const [isLoading, setIsLoading] = useState(true);
    const [thumbnailState, setThumbnailState] = useState('loading');

    function showThumbnail() {
        setTimeout(() => {
            setIsLoading(false);
            setThumbnailState('');
        }, 300);
    }

    return (

        <Popup
            trigger={
                <a rel="noopener noreferrer" className="react-card">
                    <div className="card-header">
                    <p></p>
                    {thumbnail && isLoading ? <img src="./images/loading.gif" alt="loading" /> : ''}
                        {thumbnail ? <img className={thumbnailState} onLoad={showThumbnail} src={thumbnail} alt="thumbnail" /> : ''}
                    </div>
                    <div className="card-body">
                    <p></p>
                        <h4>{title}</h4>
                        <p></p>
                        <p></p>
                        <p></p>
                    </div>

                </a>
            }
            modal
            nested
        >

            {close => (
                <div className="modal">
                    <div className="content">

                    <button
                            className="button back-btn"
                            onClick={() => {
                                console.log('modal closed ');
                                close();
                            }}
                        >
                            x
                        </button>
                        
                        {' '}
                        <div className="modal">
                            <img src={thumbnail} alt="" />
                            <div className="modal__desc">
                                <h5>{title}</h5>
                                <p>{descr} </p>
                                <div className="modal__cat">
                                    {techused}
                                </div>
                                <div className="modal__tag">
                                    tags: {tag}
                                </div>

                            </div>
                        </div>
                    </div>
                    <div className="actions">


                        <a href={to} target="_blank">
                            <button
                                className="button project-link-btn"
                            >
                                <i className="fab fa-github" aria-hidden="true" /> Project
                  </button></a>

                    </div>
                </div>

            )}
        </Popup>

    );
}

export default Card;