import React, { useState } from 'react';

import './skills.css';

function Skills({ thumbnail, title, descr}) {

    const [isLoading, setIsLoading] = useState(true);
    const [thumbnailState, setThumbnailState] = useState('loading');

    function showThumbnail() {
        setTimeout(() => {
            setIsLoading(false);
            setThumbnailState('');
        }, 300);
    }

    return (

        <a rel="noopener noreferrer" className="react-card-skills">
                    <div className="card-header">
                    <p></p>
                        {thumbnail && isLoading ? <img src="./images/loading.gif" alt="loading" /> : ''}
                        {thumbnail ? <img className={thumbnailState} onLoad={showThumbnail} src={thumbnail} alt="thumbnail" /> : ''}
                    </div>
                    <p></p>
                    <div className="card-body">
                    <p></p>
                        <h4>{title}</h4>
                        <p>{descr}</p>
                    </div>

                </a>

    );
}

export default Skills;